const gallerys = document.querySelector('.gallery');
const newImg = images.map((img) => {
          
    let { preview, original, description } = img
    return ` <li class="gallery-item">
                      <a class="gallery-link" href="${original}">
                        <img
                            class = "gallery-image"
                            src = "${preview}"
                            alt = "${description}"
                        />
                      </a>
                    </li>`;
});

gallerys.innerHTML = newImg.join('');

const lightbox = new SimpleLightbox('.gallery a', {
    captionPosition: 'bottom',
    captionDelay: '250',
    captionsData: 'alt',
    close: true,
    animationSpeed: 200,
    preloading: true,
    loop:	true,
    scaleImageToRatio: true,
    scrollZoomFactor: 0.9,
    overlay: true,
    overlayOpacity: 1.0,
    spinner:	true
});
